.videoWrapper {
  height: calc(100vh + 200px);
  width: 100vw;
  margin-top: -100px;
  overflow: hidden;
}

.outerWrapper {
  height: calc(100vh - 20px);
  width: 100vw;
  background-color: black;
  padding-top: 20px;
  overflow: hidden;
}

.playerOptions {
  position: absolute;
  top: 10px;
  left: 10px;
  height: 60px;
  width: 300px;
  display: flex;
  flex-direction: row;
}

.playerTitle {
  position: absolute;
  top: 0px;
  left: -10px;
  width: 100vw;
  text-align: center;
  color: white;
  font-family: 'Georgia, serif';
  font-style: italic;
  font-size: 36px;
}